export default "// width - conscious choice\n/* stylelint-disable order/order */\n.tui-container {\n    margin-right: auto;\n    margin-left: auto;\n\n    &.tui-container_adaptive {\n        @media @desktop-m-min {\n            width: 69rem;\n        }\n\n        @media @desktop-s {\n            width: 51.5rem;\n        }\n\n        @media @mobile-m {\n            width: 100%;\n            padding: 0 1rem;\n            box-sizing: border-box;\n        }\n    }\n\n    @media @desktop-m-min {\n        width: 69rem;\n    }\n\n    @media @desktop-s {\n        width: 51.5rem;\n        padding: 0 3rem;\n    }\n\n    &_menu {\n        @media @desktop-m-min {\n            width: 69rem;\n        }\n\n        @media @desktop-s-interval {\n            width: 51.5rem;\n        }\n\n        @media @mobile-m {\n            width: auto;\n            padding: 0 1rem;\n            box-sizing: border-box;\n        }\n    }\n\n    &_fullwidth {\n        width: auto;\n        padding: 0 1.5rem;\n\n        @media @mobile-m {\n            padding: 0 1rem;\n            box-sizing: border-box;\n        }\n    }\n\n    &_fixed {\n        @media @desktop-s {\n            width: 69rem;\n        }\n    }\n}\n/* stylelint-enable order/order */\n";